.item {
    width: 350px;
}

.item img {
    width: 95%;
    border-radius: 10px;
}

.item p {
    margin: 6px 0;
}

.item-price {
    display: flex;
    gap: 20px;
}

.item-price-new {
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}

.item-price-old {
    color: #8c8c8c;
    font-size: 18px;
    text-decoration: line-through;
}

.item:hover {
    transform: scale(1.05);
    transition: 0.6s;
}