.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.popular h1 {
    font-size: 16px;
    font-weight: 600;
    color: #171717;
}

.popular hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: black;
}

.popular-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
}
